:root {
  --color-active-link: #00acdc;
}

body {
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.logo {
  width: 40px;
  height: 40px;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  background-color: #fff;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav_list {
  flex-direction: row;
  display: flex;
}

.nav_item {
  margin: auto 10px;
  line-height: 60px;
  list-style-type: none;
  position: relative;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav_item:last-child {
  margin-right: 0;
}

.nav_link {
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
  text-transform: uppercase;
  line-height: 40px;
  display: block;
}

.nav_link:hover, .is-active {
  color: var(--color-active-link);
}

.is-active:after {
  content: "";
  background-color: var(--color-active-link);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
